<template>
  <div class="avg-monitor-chart-container">
    <div id="avg-monitor-chart" v-show="showChart"></div>
    <div v-show="showChart" class="data-source">
      <span>蜂鸟</span>
    </div>
    <span class="nodata el-table__empty-block" v-if="!showChart">暂无数据</span>
  </div>
</template>

<script>
import {queryMonitoringreport} from '@/api/monitor.js';
import {getColorObj} from '@/assets/scripts/getSomeSetting.js';

const keyToName = {
  count: '总数',
  passAvg: '通过平均值',
  avg: '申请平均值'
};
const allColorObj = getColorObj();
const colorArr = ['通过总数', '平均值通过', '平均值总计'].map(key => allColorObj[key]);
export default {
  name: 'avg-monitor-chart',
  data() {
    return {
      showChart: false
    };
  },
  created() {
    this.$on('drawAvgMonitorChart', (moduleName, varKey, monitorType) => {
      this.drawFunc(moduleName, varKey, monitorType);
    });
  },
  methods: {
    drawFunc(moduleName, varKey, monitorType) {
      this.showChart = true;
      this.getData(moduleName, varKey, monitorType)
        .then(result => {
          if (Array.isArray(result.result)) {
            if (!this.chart) {
              const elem = this.$el.querySelector('#avg-monitor-chart');
              this.chart = echarts.init(elem);
            }
            const option = this.getOption(result.result);
            this.chart.setOption(option);
          } else {
            this.showChart = false;
          }
        })
        .catch(err => {
          console.log(err);
          this.showChart = false;
        });
    },
    getData(moduleName, varKey, monitorType) {
      return queryMonitoringreport(moduleName, varKey, monitorType);
    },
    getOption(data = []) {
      if (data.length === 0) {
        this.showChart = false;
        return {};
      }
      // 整理获得需要的数据
      let avgAboveArr = data.map(item => item.avgAbove);
      let avgLowerArr = data.map(item => item.avgLower);
      let rangeCountArr = data.map(item => item.rangeCount);
      let markObj = {
        min: null,
        max: null,
        count: null
      };

      if (avgAboveArr.some(item => item != null)) {
        markObj.max = avgAboveArr[0];
      }
      if (avgLowerArr.some(item => item != null)) {
        markObj.min = avgLowerArr[0];
      }
      if (rangeCountArr.some(item => item != null)) {
        markObj.count = rangeCountArr[0];
      }

      const targetArr = data.map(item => {
        let obj = {
          count: item.count, // 总数
          avg: item.avg, // 申请平均值
          time: item.key, // 时间
          passAvg: item.passAvg // 通过平均值
        };
        Object.keys(obj).forEach(key => {
          if (typeof obj[key] === 'number') {
            if (!Number.isInteger(obj[key])) {
              obj[key] = obj[key].toFixed(2);
            }
          }
        });
        return obj;
      });
      const xData = targetArr.map(item => item.time);
      let seriesData = [];
      Object.keys(keyToName).forEach((name, i) => {
        let arr = [];
        for (let item of targetArr) {
          arr.push(item[name]);
        }
        seriesData.push({
          name: keyToName[name],
          type: i > 0 ? 'line' : 'bar',
          yAxisIndex: i > 0 ? 0 : 1,
          barGap: 0,
          smooth: true,
          data: arr
        });
      });
      for (let i = 0; i < seriesData.length; i++) {
        let item = seriesData[i];
        if (i === 0 && markObj.count != null) {
          item.markLine = {
            lineStyle: {
              normal: {
                type: 'solid',
                color: '#f00'
              }
            },
            data: [
              [
                {
                  symbol: 'none',
                  x: '85%',
                  yAxis: markObj.count
                },
                {
                  symbol: 'arrow',
                  x: '15%',
                  label: {
                    normal: {
                      position: 'middle',
                      formatter: params => {
                        return `总数参考值${params.data.yAxis}`;
                      }
                    }
                  },
                  yAxis: markObj.count,
                  yAxisIndex: 1
                }
              ]
            ]
          };
        }
        if (i > 0 && markObj.min != null) {
          item.markLine = {
            lineStyle: {
              normal: {
                type: 'solid'
              }
            },
            data: [
              {
                symbol: 'none',
                label: {
                  normal: {
                    position: 'middle',
                    formatter: '平均值监控下线{c}'
                  }
                },
                yAxis: markObj.min
              }
            ]
          };
        }
        if (i > 0 && markObj.max != null) {
          if (item.markLine && item.markLine.data) {
            item.markLine.data.push({
              symbol: 'none',
              yAxis: markObj.max,
              label: {
                normal: {
                  position: 'middle',
                  formatter: '平均值监控上线{c}'
                }
              }
            });
          } else {
            item.markLine = {
              lineStyle: {
                normal: {
                  type: 'solid'
                }
              },
              data: [
                {
                  symbol: 'none',
                  label: {
                    normal: {
                      position: 'middle'
                    }
                  },
                  yAxis: markObj.max
                }
              ]
            };
          }
        }
      }
      const option = {
        color: colorArr,
        tooltip: {
          trigger: 'axis',
          formatter: params => {
            const arr = params.map(item => {
              let str = `${item.marker} ${item.seriesName}：`;
              if (item.data || item.data === 0) {
                str += `${item.data}`;
              } else {
                str += '-';
              }
              return str;
            });
            arr.unshift(params[0].name + '时');
            return arr.join('<br />');
          }
        },
        legend: [
          {
            data: Object.values(keyToName),
            top: 0
          }
        ],
        grid: {
          top: '20%',
          left: '15%',
          right: '15%',
          bottom: '20%'
        },
        xAxis: {
          type: 'category',
          data: xData,
          scale: true,
          boundaryGap: true,
          axisLine: {
            onZero: false
          },
          splitLine: {
            show: false
          },
          splitNumber: 20,
          min: 'dataMin',
          max: 'dataMax'
        },
        yAxis: [
          {
            splitArea: {
              show: true
            },
            name: '平均值',
            axisLine: {
              lineStyle: {
                color: colorArr[1]
              }
            },
            max: function(value) {
              if (markObj.max) {
                return Math.max(value.max, markObj.max) + 1;
              } else {
                return value.max;
              }
            },
            boundaryGap: [0.2, 0.2]
          },
          {
            scale: true,
            splitArea: {
              show: true
            },
            axisLine: {
              lineStyle: {
                color: colorArr[0]
              }
            },
            name: '数量',
            min: 0,
            boundaryGap: [0.2, 0.2]
          }
        ],
        series: seriesData
      };
      if (xData.length > 30) {
        option.dataZoom = [
          {
            type: 'slider',
            y: '90%',
            start: 0,
            end: 100
          }
        ];
      }
      return option;
    }
  }
};
</script>

<style scoped>
#avg-monitor-chart {
  position: relative;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  height: 360px;
}
</style>
