                    v-if="i>0"
                  ></span>
                  <el-form-item label="开启红色预警" label-width="100px">
                    <el-switch
                      v-model="one.redstatus"
                      active-color="#f56c6c"
                      active-value="Enable"
                      inactive-value="Disable"
                    ></el-switch>
                  </el-form-item>
                  <div v-show="one.redstatus === 'Enable'">
                    <label for>浮动范围
                      <el-input placeholder="浮动范围" v-model.trim="one.range" style="width: 110px;">
                        <template slot="append">%</template>
                      </el-input>
                    </label>
                  </div>
                </div>
              </el-form-item>
              <el-form-item v-for="(special, i) in monitorData[monitorType].special" :key="i">
                <span class="title">特殊值{{i+1}}</span>
                <div class="forecast-container">
                  <el-select v-model="special.some" placeholder="请选择" style="width: 140px;">
                    <el-option
                      v-for="item in speOptions"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value"
                    ></el-option>
                  </el-select>
                  <span
                    class="btn-comm-add"
                    @click.stop="addSpe(monitorData[monitorType].special)"
                    v-if="i===0"
                  ></span>
                  <span
                    class="btn-comm-del"
                    @click.stop="delSpe(monitorData[monitorType].special,i)"
                    v-if="i>0"
                  ></span>
                </div>
                <div class="forecast-container" v-for="(one, i) in special.arr" :key="i">
                  <label for>
                    <el-select
                      v-model="one.cur"
