                      class="no-margin-left"
                      style="width: 140px;"
                    >
                      <el-option
                        v-for="item in timeOptions"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                      ></el-option>
                    </el-select>
                  </label>
                  <label for>与过去
                    <el-select v-model="one.pre" placeholder="请选择" style="width: 140px;">
                      <el-option
                        v-for="item in timeOptions"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                      ></el-option>
                    </el-select>
                  </label>
                  <label for style="margin-left:20px;">数量
                    <el-input placeholder="数量" v-model.trim="one.count" style="width: 110px;"></el-input>
                  </label>
                  <label for>占比
                    <el-input placeholder="占比" v-model.trim="one.rate" style="width: 110px;">
                      <template slot="append">%</template>
                    </el-input>
                  </label>
                  <label for>浮动范围
                    <el-input placeholder="浮动范围" v-model.trim="one.range" style="width: 110px;">
                      <template slot="append">%</template>
                    </el-input>
                  </label>
                  <span class="btn-comm-add" @click.stop="addSpeArr(special.arr)" v-if="i===0"></span>
                  <span class="btn-comm-del" @click.stop="delSpeArr(special.arr,i)" v-if="i>0"></span>
                  <el-form-item label="开启红色预警" label-width="100px">
                    <el-switch
                      v-model="one.redstatus"
                      active-color="#f56c6c"
                      active-value="Enable"
                      inactive-value="Disable"
                    ></el-switch>
                  </el-form-item>
                  <div v-show="one.redstatus === 'Enable'">
                    <label for>占比
                      <el-input placeholder="占比" v-model.trim="one.redrate" style="width: 110px;">
                        <template slot="append">%</template>
                      </el-input>
                    </label>
                    <label for>浮动范围
                      <el-input
                        placeholder="浮动范围"
                        v-model.trim="one.redrange"
                        style="width: 110px;"
                      >
                        <template slot="append">%</template>
                      </el-input>
                    </label>
                  </div>
                </div>
              </el-form-item>
              <el-form-item>
                <span class="title">批量值</span>
                <div
                  class="forecast-container"
                  v-for="(one, i) in monitorData[monitorType].batch"
                  :key="i"
                >
                  <label for>
                    <el-select
                      v-model="one.cur"
                      placeholder="请选择"
                      class="no-margin-left"
                      style="width: 140px;"
                    >
                      <el-option
                        v-for="item in timeOptions"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                      ></el-option>
                    </el-select>
                  </label>
                  <label for>与过去
                    <el-select v-model="one.pre" placeholder="请选择" style="width: 140px;">
                      <el-option
                        v-for="item in timeOptions"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                      ></el-option>
                    </el-select>
                  </label>
                  <label for style="margin-left:20px;">数量
                    <el-input placeholder="数量" v-model.trim="one.count" style="width: 110px;"></el-input>
                  </label>
                  <label for>占比
                    <el-input placeholder="占比" v-model.trim="one.rate" style="width: 110px;">
                      <template slot="append">%</template>
                    </el-input>
                  </label>
                  <label for>浮动范围
                    <el-input placeholder="浮动范围" v-model.trim="one.range" style="width: 110px;">
                      <template slot="append">%</template>
                    </el-input>
                  </label>
                  <span
                    class="btn-comm-add"
                    @click.stop="addBat(monitorData[monitorType].batch)"
                    v-if="i===0"
                  ></span>
                  <span
                    class="btn-comm-del"
                    @click.stop="delBat(monitorData[monitorType].batch, i)"
                    v-if="i>0"
                  ></span>
                  <el-form-item label="开启红色预警" label-width="100px">
                    <el-switch
                      v-model="one.redstatus"
                      active-color="#f56c6c"
                      active-value="Enable"
                      inactive-value="Disable"
                    ></el-switch>
                  </el-form-item>
                  <div v-show="one.redstatus === 'Enable'">
                    <label for>占比
                      <el-input placeholder="占比" v-model.trim="one.redrate" style="width: 110px;">
                        <template slot="append">%</template>
                      </el-input>
                    </label>
                    <label for>浮动范围
                      <el-input
                        placeholder="浮动范围"
                        v-model.trim="one.redrange"
                        style="width: 110px;"
                      >
                        <template slot="append">%</template>
                      </el-input>
                    </label>
                  </div>
                </div>
              </el-form-item>
            </div>
            <el-form-item>
              <el-button type="primary" @click.prevent.stop="saveVarsetFunc">确定</el-button>
            </el-form-item>
          </div>
        </el-form>
      </el-card>
      <div v-show="showsave" class="box-save-export">
        <el-button type="primary" @click.stop.prevent="saveResult">保存</el-button>
      </div>
      <filename @save="saveFile" :init-visible.sync="showfilesave"></filename>
    </div>
  </div>
</template>

<script>
import dayjs from 'dayjs';
import datepicker from '@/components/datepicker';
import sampleSelect from '@/components/sample-select.vue';
import varSelect from '@/components/var-select.vue';
import sampleChart from '@/components/sample-chart';
import univariateMonitorChart from '@/components/univariate-monitor-chart';
import univariateDistributionChart from '@/components/univariate-distribution-chart';
import avgMonitorChart from '@/components/avg-monitor-chart';
import speMonitorChart from '@/components/spe-monitor-chart';
import filename from '@/components/filename';
import {getMonitorData} from '@/assets/scripts/getSomeSetting.js';
import {getVarList} from '@/api/sample.js';
import {saveAnalysis, queryAnalysisDetail} from '@/api/analysis.js';
import {getVarMonitorSetting, getVarMonitorListSetting, setVarMonitorSetting} from '@/api/monitor.js';

export default {
  name: 'univariate-monitor',
  components: {
    datepicker,
    sampleSelect,
    varSelect,
    sampleChart,
    univariateMonitorChart,
    univariateDistributionChart,
    avgMonitorChart,
    speMonitorChart,
    filename
  },
  data() {
    return {
      showsave: false,
      showfilesave: false,
      initSample: [],
      initVar: [],
      decisionDates: [
        dayjs()
          .add(-3, 'month')
          .format('YYYY-MM-DD'),
        dayjs().format('YYYY-MM-DD')
      ],
      moduleName: '',
      moduleList: [],
      varList: [],
      monitorList: [],
      moduleDataList: [],
      varKey: '',
      allVarset: {
        Pass: '平均值（通过）',
        Refuse: '平均值（拒绝）',
        Avg: '平均值（总计）',
        T5Overdue: '平均值（T5指标逾期）',
        NonT5overdue: '平均值（非T5指标逾期）'
      },
      id: '',
      textOptions: [
        {
          label: '-1',
          value: '-1'
        },
        {
          label: '是',
          value: '是'
        },
        {
          label: '否',
          value: '否'
        }
      ],
      floatOptions: [
        {
          label: '是',
          value: '是'
        },
        {
          label: '否',
          value: '否'
        },
        {
          label: '2',
          value: '2'
        },
        {
          label: '1',
          value: '1'
        },
        {
          label: '0',
          value: '0'
        },
        {
          label: '-1',
          value: '-1'
        },
        {
          label: '-9999',
          value: '-9999'
        },
        {
          label: '-999999',
          value: '-999999'
        }
      ],
      speOptions: [
        {
          label: '-1',
          value: '-1'
        },
        {
          label: '-9999',
          value: '-9999'
        },
        {
          label: '-999999',
          value: '-999999'
        }
      ],
      timeOptions: [
        {
          value: 'hour',
          label: '近一个小时'
        },
        {
          value: 'day',
          label: '近一天'
        },
        {
          value: 'week',
          label: '近一周'
        },
        {
          value: 'month',
          label: '近一个月'
        }
      ],
      monitorData: getMonitorData()
    };
  },
  computed: {
    showPart() {
      return this.$store.state.monitorPage.univariate;
    },
    parmDes() {
      const theOne = this.varList.find(item => item.value === this.varKey) || {};
      return `${this.moduleName} ${theOne.label}`;
    },
    isFloat() {
      const theOne = this.varList.find(item => item.value === this.varKey) || {};
      return theOne.fieldtype === 'float';
    }
  },
  watch: {
    showPart: function(newV) {
      if (newV === 'form') {
        let initSample = this.initSample;
        this.$nextTick(() => {
          if (!this.$refs.sampleChart.showChart) {
            this.initSample = [];
            this.$refs.sampleChart.$emit('drawSampleChart', this.moduleName);
            this.$nextTick(() => {
              this.initSample = initSample;
              console.log(this.initSample);
            });
          }
        });
      }
    },
    $route: {
      handler: function(to, from) {
        if (
          from &&
          ((from.name === 'univariate-monitor-view' && to.name === 'univariate-monitor') ||
            (from.name === 'univariate-monitor' && to.name === 'univariate-monitor-view'))
        ) {
          this.$store.commit('UPDATE_MONITORPAGE', {
            key: 'univariate',
            value: 'form'
          });
          this.$router.replace({
            path: '/empty',
            query: {
              callbackurl: to.path
            }
          });
        }
      },
      immediate: true
    },
    'monitorData.monitorType': function(newV, oldV) {
      if (!oldV) {
        return;
      }
      this.$refs.avgMonitorChart.$emit('drawAvgMonitorChart', this.moduleName, this.varKey, newV);
      this.$refs.speMonitorChart.$emit('drawSpeMonitorChart', this.moduleName, this.varKey, newV);
    }
  },
  created() {
    this.recordId = this.$route.params.recordid;
    if (this.recordId) {
      this.$store.commit('UPDATE_MONITORPAGE', {
        key: 'univariate',
        value: 'result'
      });
      this.getAnalysisResult();
    }
  },
  mounted() {
    let modulename = this.$route.query.modulename;
    let productname = this.$route.query.productname;
    let moduletype = String(this.$route.query.moduletype);
    let variablekey = this.$route.query.variablekey;
    let transmittype = this.$route.query.transmittype;
    if (
      modulename != null &&
      productname != null &&
      moduletype != null &&
      variablekey != null &&
      transmittype != null
    ) {
      let initVar = [transmittype, variablekey];
      this.params = {initVar};
      this.$nextTick(() => {
        this.getVars([productname, moduletype, modulename], true);
        this.changeVar(initVar);
      });
    }
  },
  methods: {
    initVarset() {
      return getMonitorData();
    },
    returnPrev() {
      this.$store.commit('UPDATE_MONITORPAGE', {
        key: 'univariate',
        value: 'form'
      });
    },
    addAvg(arr) {
      if (arr.length >= 4) {
        this.$message.warning('最多选择4个');
        return;
      }
      arr.push({
        cur: 'hour',
        pre: 'day',
        range: ''
      });
    },
    delAvg(arr, i) {
      arr.splice(i, 1);
    },
    addSpe(arr) {
      if (arr.length >= 2) {
        this.$message.warning('最多选择2个');
        return;
      }
      arr.push({
        some: '',
        arr: [
          {
            cur: 'hour',
            pre: 'day',
            rate: '',
            range: ''
          }
        ]
      });
    },
    delSpe(arr, i) {
      arr.splice(i, 1);
    },
    addSpeArr(item) {
      if (item.length >= 4) {
        this.$message.warning('最多选择4个');
        return;
      }
      item.push({
        cur: 'hour',
        pre: 'day',
        rate: '',
        range: ''
      });
    },
    delSpeArr(item, i) {
      item.splice(i, 1);
    },
    addBat(arr) {
      if (arr.length >= 4) {
        this.$message.warning('最多选择4个');
        return;
      }
      arr.push({
        cur: 'hour',
        pre: 'day',
        rate: '',
        range: ''
      });
    },
    delBat(arr, i) {
      arr.splice(i, 1);
    },
    getVars(val, isInit = false) {
      this.selectVar = [];
      this.rcProductName = val[0];
      this.moduleType = val[1];
      this.moduleName = val[2];
      this.initSample = val;
      if (!isInit) {
        this.$refs.sampleChart.$emit('drawSampleChart', this.moduleName);
      }
      getVarList(this.moduleName)
        .then(result => {
          if (Array.isArray(result.result)) {
            if (!result.result.length) {
              return;
            }
            this.varList = result.result.map(({key, vbname, fieldtype, transmittype}) => {
              return {
                label: `${vbname}${fieldtype === 'float' ? '（连续）' : '（离散）'}`,
                value: key,
                fieldtype,
                transmittype
              };
            });
            if (isInit) {
              this.initVar = this.params.initVar;
              this.searchFunc();
            }
          }
        })
        .catch(err => console.log(err));
    },
    changeVar(val) {
      this.initVar = val;
      this.varKey = val[1];
    },
    searchFunc() {
      if (!this.moduleName) {
        this.$message.warning('请选择模块名称');
        return;
      }
      if (!this.varKey) {
        this.$message.warning('请选择变量名称');
        return;
      }
      if (!this.decisionDates[0]) {
        this.$message.warning('请选择起始时间');
        return;
      }
      if (!this.decisionDates[1]) {
        this.$message.warning('请选择结束时间');
        return;
      }

      this.$store.commit('UPDATE_MONITORPAGE', {
        key: 'univariate',
        value: 'result'
      });

      this.$nextTick(() => {
        this.showsave = true;
        try {
          this.$refs.univariateMonitorChart.$emit(
            'drawUnivariateMonitorChart',
            this.moduleName,
            this.varKey,
            this.decisionDates[0],
            this.decisionDates[1]
          );
          this.$refs.avgMonitorChart.$emit(
            'drawAvgMonitorChart',
            this.moduleName,
            this.varKey,
            this.monitorData.monitorType || this.$route.query.monitortype
          );
          this.$refs.speMonitorChart.$emit(
            'drawSpeMonitorChart',
            this.moduleName,
            this.varKey,
            this.monitorData.monitorType || this.$route.query.monitortype
          );
          let theVar = this.varList.find(item => item.value === this.varKey) || {};
          this.$refs.univariateDistributionChart.$emit(
            'drawUnivariateDistributioChart',
            this.initSample,
            theVar,
            this.decisionDates
          );
        } catch (err) {
          this.showsave = false;
        }
        this.queryMonitorList();
      });
    },
    queryMonitorList() {
      getVarMonitorListSetting(this.moduleName, this.varKey)
        .then(result => {
          if (Array.isArray(result.result)) {
            if (result.result.length > 0) {
              this.monitorData.monitorStatus = true;
            }
            for (let item of result.result) {
              const {monitorType, id, min, max, straggling, avg, special, batch, status} = item;
              Object.assign(this.monitorData[monitorType], {
                id,
                monitorStatus: status === 'Enable',
                min: typeof min === 'number' ? min : '',
                max: typeof max === 'number' ? max : '',
                straggling,
                avg:
                  Array.isArray(avg) && avg.length > 0
                    ? avg
                    : [
                        {
                          cur: 'hour',
                          pre: 'day',
                          range: ''
                        }
                      ],
                special:
                  Array.isArray(special) && special.length > 0
                    ? special
                    : [
                        {
                          some: '',
                          arr: [
                            {
                              cur: 'hour',
                              pre: 'day',
                              range: ''
                            }
                          ]
                        }
                      ],
                batch:
                  Array.isArray(batch) && batch.length > 0
                    ? batch
                    : [
                        {
                          cur: 'hour',
                          pre: 'day',
                          rate: '',
                          range: ''
                        }
                      ]
              });
            }
          }
          if (this.$route.query.monitortype) {
            this.monitorData.monitorType = this.$route.query.monitortype;
          }
        })
        .catch(err => {
          console.log(err);
          this.showsave = false;
          if (!this.monitorData.monitorType) {
            this.monitorData = this.initVarset();
          }
        });
    },
    saveVarsetFunc() {
      let theOne = this.varList.find(item => item.value === this.varKey) || {};
      const monitorData = this.monitorData[this.monitorData.monitorType];
      const postData = {
        id: monitorData.id,
        status: this.monitorData.monitorStatus ? 'Enable' : 'Disable',
        fieldtype: this.isFloat ? 'continuous' : 'discrete',
        monitorType: this.monitorData.monitorType,
        straggling: monitorData.straggling,
        min: monitorData.min,
        max: monitorData.max,
        special: monitorData.special,
        avg: monitorData.avg,
        batch: monitorData.batch,
        productName: this.initSample[0],
        moduletype: this.initSample[1],
        modulename: this.moduleName,
        transmittype: this.initVar[0],
        variablekey: this.varKey,
        variablename: theOne.label
      };
      setVarMonitorSetting(postData)
        .then(result => {
          monitorData.id = result.result;
          this.$message.success('保存成功');
        })
        .catch(err => {
          console.log(err);
        });
    },
    saveResult() {
      if (!this.filesaveid) {
        this.showfilesave = true;
        return;
      }
      this.saveFile(this.filename);
    },
    saveFile(name) {
      let data = {};
      let theOne = this.varList.find(item => item.value === this.varKey) || {};
      data.parmDes = `${this.moduleName} ${theOne.label}`;
      data.name = name;
      data.type = 2;
      data.id = this.filesaveid;
      const params = {
        initSample: this.initSample,
        initVar: this.initVar,
        modulename: this.moduleName,
        variablekey: this.varKey,
        startTime: this.decisionDates[0],
        endTime: this.decisionDates[1]
      };
      data.result = JSON.stringify(params);
      saveAnalysis(data)
        .then(result => {
          this.$message.success('保存成功');
          this.filename = data.name;
          this.filesaveid = result.result;
        })
        .catch(err => console.log(err));
    },
    getAnalysisResult() {
      queryAnalysisDetail(this.recordId)
        .then(result => {
          if (result.result == null) {
            this.$message.warning('此历史记录不存在');
            return;
          }
          this.dealWithParams(result.result);
        })
        .catch(err => console.log(err));
    },
    dealWithParams(result) {
      this.filename = result.name;
      this.filesaveid = result.id;
      const params = JSON.parse(result.result);
      this.initSample = params.initSample;
      this.decisionDates = [params.startTime, params.endTime];
      this.varKey = params.variablekey;
      this.moduleName = params.modulename;
      this.params = params;

      this.getVars(this.initSample, true);
    }
  }
};
</script>

