<template>
  <div class="spe-monitor-chart-container">
    <div id="spe-monitor-chart" v-show="showChart"></div>
    <div v-show="showChart" class="data-source">
      <span>蜂鸟</span>
    </div>
    <span class="nodata el-table__empty-block" v-if="!showChart">暂无数据</span>
  </div>
</template>

<script>
import {queryMonitoringspecialreport} from '@/api/monitor.js';
import {getColorObj} from '@/assets/scripts/getSomeSetting.js';

const keyToName = {
  rate1: '-1占比',
  count1: '-1数量',
  rate2: '-9999占比',
  count2: '-9999数量',
  rate3: '-999999占比',
  count3: '-999999数量'
};
const allColorObj = getColorObj();
const colorArr = ['-1占比', '-1数量', '-9999占比', '-9999数量', '-999999占比', '-999999数量'].map(
  key => allColorObj[key]
);
export default {
  name: 'spe-monitor-chart',
  data() {
    return {
      showChart: false
    };
  },
  created() {
    this.$on('drawSpeMonitorChart', (moduleName, varKey, monitorType) => {
      this.drawFunc(moduleName, varKey, monitorType);
    });
  },
  methods: {
    drawFunc(moduleName, varKey, monitorType) {
      this.showChart = true;
      this.getData(moduleName, varKey, monitorType)
        .then(result => {
          if (Array.isArray(result.result)) {
            if (!this.chart) {
              const elem = this.$el.querySelector('#spe-monitor-chart');
              this.chart = echarts.init(elem);
            }
            const option = this.getOption(result.result);
            this.chart.setOption(option);
          } else {
            this.showChart = false;
          }
        })
        .catch(err => {
          console.log(err);
          this.showChart = false;
        });
    },
    getData(moduleName, varKey, monitorType) {
      return queryMonitoringspecialreport(moduleName, varKey, monitorType);
    },
    getOption(data = []) {
      if (data.length === 0) {
        this.showChart = false;
        return {};
      }
      // 整理获得需要的数据
      const targetArr = data.map(item => {
        item.size = item.datas.length;
        item.datas.sort((pre, cur) => {
          return Number(cur.some) - Number(pre.some);
        });
        let obj = {
          time: item.key
        };
        for (let one of item.datas) {
          if (one.some === '-1') {
            obj.count1 = one.count;
            obj.rate1 = one.rate * 100;
          } else if (one.some === '-9999') {
            obj.count3 = one.count;
            obj.rate3 = one.rate * 100;
          } else if (one.some === '-999999') {
            obj.count3 = one.count;
            obj.rate3 = one.rate * 100;
          }
        }
        Object.keys(obj).forEach(key => {
          if (typeof obj[key] === 'number') {
            if (!Number.isInteger(obj[key])) {
              obj[key] = obj[key].toFixed(2);
            }
          }
        });
        return obj;
      });
      data.sort((pre, cur) => cur.size - pre.size);
      const xData = targetArr.map(item => item.time);
      const markObj = {};
      let theOne = data[0];
      for (let item of data) {
        if (theOne.datas.length < item.datas.length) {
          theOne = item;
        }
      }
      for (let item of theOne.datas) {
        markObj[item.some] = {
          min: item.rangeLower,
          max: item.rangeAbove,
          count: item.rangeCount,
          rate: item.rangeRate
        };
      }
      let seriesData = [];
      Object.keys(keyToName).forEach((name, i) => {
        let arr = [];
        for (let item of targetArr) {
          arr.push(item[name]);
        }
        seriesData.push({
          name: keyToName[name],
          type: i % 2 === 0 ? 'line' : 'bar',
          yAxisIndex: i % 2,
          lineStyle: {
            width: 1
          },
          barGap: 0,
          smooth: true,
          data: arr
        });
      });
      for (let i = 0; i < seriesData.length; i++) {
        let item = seriesData[i];
        let isExits = item.data.some(one => one != undefined);
        if (isExits) {
          if (i % 2 === 0) {
            let key = i === 0 ? '-1' : i === 2 ? '-9999' : '-999999';
            if (markObj[key] && markObj[key].min != null) {
              item.markLine = {
                data: [
                  {
                    lineStyle: {
                      normal: {
                        type: 'solid'
                      }
                    },
                    symbol: 'none',
                    yAxis: markObj[key].min * 100,
                    label: {
                      normal: {
                        position: 'middle',
                        formatter: `${key}浮动范围最小值{c}%`
                      }
                    }
                  },
                  {
                    lineStyle: {
                      normal: {
                        type: 'solid'
                      }
                    },
                    symbol: 'none',
                    yAxis: markObj[key].max * 100,
                    label: {
                      normal: {
                        position: 'middle',
                        formatter: `${key}浮动范围最大值{c}%`
                      }
                    }
                  }
                ]
              };
            }
          }

          let key = i / 2 < 1 ? '-1' : i / 2 < 2 ? '-9999' : '-999999';
          let key2 = i % 2 ? 'count' : 'rate';
          if (markObj[key] && markObj[key][key2] != null) {
            if (item.markLine && item.markLine.data) {
              item.markLine.data.push([
                {
                  lineStyle: {
                    normal: {
                      type: 'solid'
                    }
                  },
                  symbol: 'none',
                  x: i % 2 ? '85%' : '15%',
                  yAxis: markObj[key][key2],
                  yAxisIndex: (i % 2) + 1
                },
                {
                  x: i % 2 ? '15%' : '85%',
                  yAxis: markObj[key][key2],
                  yAxisIndex: (i % 2) + 1,
                  label: {
                    normal: {
                      position: 'middle',
                      formatter: params => {
                        return `${key}${key2 === 'rate' ? '占比' : '数量'}参考值${params.data.yAxis}${
                          key2 === 'rate' ? '%' : ''
                        }`;
                      }
                    }
                  }
                }
              ]);
            } else {
              item.markLine = {
                lineStyle: {
                  normal: {
                    type: 'solid'
                  }
                },
                data: [
                  [
                    {symbol: 'none', x: i % 2 ? '85%' : '15%', yAxis: markObj[key][key2]},
                    {
                      x: i % 2 ? '15%' : '85%',
                      label: {
                        normal: {
                          position: 'middle',
                          formatter: params => {
                            return `${key}${key2 === 'rate' ? '占比' : '数量'}参考值${params.data.yAxis}${
                              key2 === 'rate' ? '%' : ''
                            }`;
                          }
                        }
                      },
                      yAxis: markObj[key][key2],
                      yAxisIndex: (i % 2) + 1
                    }
                  ]
                ]
              };
            }
          }
        }
      }
      const option = {
        color: colorArr,
        tooltip: {
          trigger: 'axis',
          formatter: params => {
            const arr = params.map(item => {
              let str = `${item.marker} ${item.seriesName}：`;
              if (typeof item.data !== 'undefined') {
                str += `${item.data}${item.seriesIndex % 2 === 0 ? '%' : ''}`;
              } else {
                str += '-';
              }
              return str;
            });
            arr.unshift(params[0].name + '时');
            return arr.join('<br />');
          }
        },
        legend: [
          {
            data: Object.values(keyToName),
            top: 0
          }
        ],
        grid: {
          top: '20%',
          left: '15%',
          right: '15%',
          bottom: '20%'
        },
        xAxis: {
          type: 'category',
          data: xData,
          scale: true,
          boundaryGap: true,
          axisLine: {
            onZero: false
          },
          splitLine: {
            show: false
          },
          splitNumber: 20
        },
        yAxis: [
          {
            scale: true,
            splitArea: {
              show: true
            },
            name: '占比（%）',
            max: function(value) {
              if (markObj['-1'].rate) {
                return Math.max(value.max, markObj['-1'].rate, markObj['-1'].max * 100) + 1;
              } else {
                return value.max;
              }
            },
            axisLine: {
              lineStyle: {
                color: colorArr[0]
              }
            },
            min: 0,
            boundaryGap: [0.2, 0.2]
          },
          {
            scale: true,
            splitArea: {
              show: true
            },
            name: '数量',
            max: function(value) {
              if (markObj['-1'].count) {
                return Math.max(value.max, markObj['-1'].count) + 1;
              } else {
                return value.max;
              }
            },
            axisLine: {
              lineStyle: {
                color: colorArr[1]
              }
            },
            min: 0,
            boundaryGap: [0.2, 0.2]
          }
        ],
        series: seriesData
      };
      if (xData.length > 30) {
        option.dataZoom = [
          {
            type: 'slider',
            y: '90%',
            start: 0,
            end: 100
          }
        ];
      }
      return option;
    }
  }
};
</script>

<style scoped>
#spe-monitor-chart {
  position: relative;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  height: 360px;
}
</style>
