<template>
  <div class="univariate-distribution-chart-container">
    <div id="univariate-distribution-chart" v-show="showChart"></div>
    <div v-show="showChart" class="data-source">
      <span>蜂鸟</span>
    </div>
    <span class="nodata el-table__empty-block" v-if="!showChart">暂无数据</span>
  </div>
</template>

<script>
import dayjs from 'dayjs';
import {queryVarAutoSplit} from '@/api/analysis.js';
import {getColorObj} from '@/assets/scripts/getSomeSetting.js';
import {queryDecision} from '@/api/decision.js';

const allColorObj = getColorObj();
const colorArr = ['占比', '一个月前占比', '逾期率', '一个月前逾期率'].map(key => allColorObj[key]);
export default {
  name: 'univariate-distribution-chart',
  data() {
    return {
      showChart: false
    };
  },
  created() {
    this.$on('drawUnivariateDistributioChart', (initSample, theVar, decisionDates) => {
      this.initSample = initSample;
      this.decisionDates = decisionDates;
      this.drawFunc(theVar);
    });
  },
  methods: {
    drawFunc(theVar) {
      this.showChart = true;
      this.getData(theVar)
        .then(result => {
          const data = this.dealWithData(result);
          if (Array.isArray(data) && data.length > 0) {
            if (!this.chart) {
              const elem = this.$el.querySelector('#univariate-distribution-chart');
              this.chart = echarts.init(elem);
            }
            const option = this.getOption(data);
            this.chart.setOption(option);
          } else {
            this.showChart = false;
          }
        })
        .catch(err => {
          console.log(err);
          this.showChart = false;
        });
    },
    getData(theVar) {
      return this.setVar(theVar).then(result => {
        this.theVar = result;
        return this.getT10Data(result);
      });
    },
    setVar(theVar) {
      return new Promise((resolve, reject) => {
        if (theVar.fieldtype === 'float') {
          queryVarAutoSplit(this.initSample[2], theVar.value)
            .then(result => {
              if (result.result && Array.isArray(result.result.variableAreaRangeList)) {
                theVar.rangeList = result.result.variableAreaRangeList;
                theVar.statisticType = 'SMART_RANGE';
              }
              resolve(theVar);
            })
            .catch(err => {
              console.log(err);
              reject(theVar);
            });
        } else {
          resolve(theVar);
        }
      });
    },
    getT10Data(item) {
      const data = {
        indexName: [this.initSample[2]],
        rcProductName: this.initSample[0],
        moduleType: String(this.initSample[1]),
        variablekey: item.value,
        createTimeStart: this.decisionDates[0],
        createTimeEnd: this.decisionDates[1],
        statisticConditionList: [
          {
            fieldName: item.value,
            statisticType: item.statisticType || 'FIELD_GROUP',
            rangeList: item.rangeList || []
          }
        ]
      };
      // 占比计数百分比
      let p1 = this.getStatistic('BEFORE_LOAN', data);
      // 一个月前占比
      let p2 = this.getStatistic(
        'BEFORE_LOAN',
        Object.assign({}, data, {
          createTimeStart: dayjs(this.decisionDates[0])
            .add(-1, 'month')
            .format('YYYY-MM-DD'),
          createTimeEnd: dayjs(this.decisionDates[0])
            .add(-1, 'day')
            .format('YYYY-MM-DD')
        })
      );
      // t10逾期率
      let p3 = this.getStatistic('AFTER_LOAN', data);
      // 一个月前t10逾期率
      let p4 = this.getStatistic(
        'AFTER_LOAN',
        Object.assign({}, data, {
          createTimeStart: dayjs(this.decisionDates[0])
            .add(-1, 'month')
            .format('YYYY-MM-DD'),
          createTimeEnd: dayjs(this.decisionDates[0])
            .add(-1, 'day')
            .format('YYYY-MM-DD')
        })
      );
      return Promise.all([p1, p2, p3, p4]);
    },
    getStatistic(type, data) {
      let reqData = Object.assign({}, data, {
        resultValueType: 'COUNT',
        filterConditionList: [],
        resultField: '',
        resultNeedPercent: true
      });
      if (type === 'BEFORE_LOAN') {
        Object.assign(reqData, {
          loanType: 'BEFORE_LOAN'
        });
      } else if (type === 'AFTER_LOAN') {
        Object.assign(reqData, {
          afterLoanKPI: 'T10',
          overdueDays: 'null',
          loanType: 'AFTER_LOAN'
        });
      }
      return queryDecision(reqData);
    },
    dealWithData(results) {
      let beforeData = [];
      let afterData = [];
      let beforeData1 = [];
      let afterData1 = [];
      if (
        results[0].result &&
        Array.isArray(results[0].result.originalReport) &&
        results[0].result.originalReport.length
      ) {
        beforeData = results[0].result.originalReport;
      }
      if (
        results[1].result &&
        Array.isArray(results[1].result.originalReport) &&
        results[1].result.originalReport.length
      ) {
        beforeData1 = results[1].result.originalReport;
      }
      if (
        results[2].result &&
        Array.isArray(results[2].result.originalReport) &&
        results[2].result.originalReport.length
      ) {
        afterData = results[2].result.originalReport;
      }
      if (
        results[3].result &&
        Array.isArray(results[3].result.originalReport) &&
        results[3].result.originalReport.length
      ) {
        afterData1 = results[3].result.originalReport;
      }
      const arr = beforeData.map(val => {
        let theOne = beforeData1.find(one => one.key === val.key);
        let theOne1 = afterData.find(one => one.key === val.key);
        let theOne2 = afterData1.find(one => one.key === val.key);
        let t10V, t10V1, t10V2;
        if (theOne) {
          t10V = parseFloat(theOne.value);
          if (typeof t10V === 'number') {
            t10V = Number.isInteger(t10V) ? t10V : t10V.toFixed(2);
          }
        }
        if (theOne1) {
          t10V1 = parseFloat(theOne1.value);
          if (typeof t10V1 === 'number') {
            t10V1 = Number.isInteger(t10V1) ? t10V1 : t10V1.toFixed(2);
          }
        }
        if (theOne2) {
          t10V2 = parseFloat(theOne2.value);
          if (typeof t10V2 === 'number') {
            t10V2 = Number.isInteger(t10V2) ? t10V2 : t10V2.toFixed(2);
          }
        }
        return {
          key: val.key,
          v1: parseFloat(val.value),
          v2: t10V,
          v3: t10V1,
          v4: t10V2
        };
      });
      return arr;
    },
    getOption(data = []) {
      // 整理获得需要的数据
      let option = {
        title: {
          top: 0,
          text: this.theVar.label + '分布图',
          left: 'center',
          textStyle: {
            fontWeight: 'bold',
            fontSize: 16
          }
        },
        color: colorArr,
        legend: {
          data: ['占比', '一个月前占比', '逾期率', '一个月前逾期率'],
          top: 40,
          left: 60
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          },
          formatter: params => {
            const arr = params.map(item => {
              return `${item.marker} ${item.seriesName}：${item.data}%`;
            });
            arr.unshift(params[0].name);
            return arr.join('<br />');
          },
          padding: [5, 12]
        },
        grid: {
          top: '30%',
          left: 60,
          right: 70,
          bottom: '20%',
          containLabel: true
        },
        xAxis: [
          {
            type: 'category',
            name: '变量取值',
            nameGap: 30,
            nameLocation: 'center',
            scale: true,
            boundaryGap: true,
            axisLine: {
              onZero: false
            },
            splitLine: {
              show: false
            },
            // splitNumber: 20,
            data: data.map(v => v.key)
          }
        ],
        yAxis: [
          {
            name: '占比（%）',
            scale: true,
            splitArea: {
              show: true
            },
            axisLine: {
              lineStyle: {
                color: colorArr[0]
              }
            },
            min: 0,
            boundaryGap: [0.2, 0.2]
          },
          {
            name: '逾期率（%）',
            scale: true,
            splitArea: {
              show: true
            },
            axisLine: {
              lineStyle: {
                color: colorArr[1]
              }
            },
            min: 0,
            position: 'right',
            boundaryGap: [0.2, 0.2]
          }
        ],
        series: [
          {
            name: '占比',
            type: 'bar',
            yAxisIndex: 0,
            barGap: 0,
            data: data.map(v => v.v1)
          },
          {
            name: '一个月前占比',
            type: 'bar',
            yAxisIndex: 0,
            data: data.map(v => v.v2)
          },
          {
            name: '逾期率',
            type: 'line',
            yAxisIndex: 1,
            data: data.map(v => v.v3)
          },
          {
            name: '一个月前逾期率',
            type: 'line',
            yAxisIndex: 1,
            data: data.map(v => v.v4)
          }
        ]
      };
      if (option.xAxis[0].data.length > 30) {
        option.dataZoom = [
          {
            type: 'slider',
            y: '90%',
            start: 0,
            end: 100
          }
        ];
      }
      return option;
    }
  }
};
</script>

<style scoped>
#univariate-distribution-chart {
  position: relative;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  height: 360px;
}
</style>
